import React, { useState, useEffect } from 'react'
import { Link, useParams } from 'react-router-dom'
import { ReactComponent as ArrowLeft } from '../assets/arrow-left.svg'
// import notes from '../assets/data'

const NotePage = () => {
    let params = useParams()
    let noteId = params.noteId

    let [note, setNote] = useState(null)

    useEffect(() => {
        getNote()
    }, [noteId])
    // let note = notes.find(note => note.id === noteId)

    let getNote = async () => {
        if (noteId === 'new') return

        let response = await fetch(`http://127.0.0.1:5000/notes/${noteId}`)
        let data = await response.json()
        setNote(data)
    }

    let createNote = async () => {
        await fetch('http://127.0.0.1:5000/notes/', {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json'
            },
            body: JSON.stringify({ ...note, 'updated': new Date() })
        })
    }

    let updateNote = async () => {
        await fetch(`http://127.0.0.1:5000/notes/${noteId}`, {
            method: 'PUT',
            headers: {
                'Content-Type': 'application/json'
            },
            body: JSON.stringify({ ...note, 'updated': new Date() })
        })
    }

    let deleteNote = async () => {
        await fetch(`http://127.0.0.1:5000/notes/${noteId}`, {
            method: 'DELETE',
            headers: {
                'Content-Type': 'application/json'
            },
            body: JSON.stringify(note)
        })
    }

    let handleSubmit = () => {
        if (noteId !== 'new' && !note.body) {
            deleteNote()
        } else if (noteId !== 'new') {
            updateNote()
        } else if (noteId === 'new' && note !== null) {
            createNote()
        }
    }

    return (
        <div className='note'>

            <div className='note-header'>
                <h3>
                    <Link to='/'>
                        <ArrowLeft onClick={handleSubmit} />
                    </Link>
                </h3>
                {noteId !== 'new' ? (
                    <Link to='/' onClick={deleteNote}>Delete</Link>
                ) : (
                    <Link to='/' onClick={handleSubmit}>Done</Link>
                )}
            </div>
            <textarea onChange={(e) => { setNote({ ...note, 'body': e.target.value }) }} value={note?.body}></textarea>
        </div>
    )
}

export default NotePage
